.floatingDiv{
    display: flex;
    justify-content: space-around;
    background-color: white;
    box-shadow: var(--boxShadow);
    border-radius: 17px;
    align-items: center;
    padding: 0px 28px 0px 0px ;
    height: 4rem;
}
.floatingDiv>img{
    transform: scale(0.4);
}
.floatingDiv>span{
    font-family: sans-serif;
    font-size: 16px;
}